
.App {
  text-align: center;
  background-image: url('./bg.jpeg');
  position: fixed;
  min-width: 100%;
  min-height: 100%;
  background-size: cover;
  background-position: center;
}

.App .divNav{
  
  border-bottom: 5px solid white;
  margin-left: 5%;
  margin-right: 5%;
  margin-top: 2%;
  padding: 0%;
  
}

.App .paraDiv{
  margin-top: 13%;
  margin-left: 5%;
  display: block;
  text-align: left;
  
  font-size: 1.5em;
  
}


.App .paraDiv h1{
  
  padding-bottom: 0%;
  margin-bottom: 1%;
}

@media (max-width: 1000px)
{
  .App .paraDiv{
    margin-top: 35%;
    justify-content: center;
  }
}
.App .ButtonDiv{
  margin-left: 8%;
  text-align: left;
  margin-top: 3%;
}
.App .ButtonDiv button{
  

  color:white;
  font-size: 140%;
  height: 2.5em;
  width: 9em;
  background-color: #f0b520;
  border-color: #f0b520;
  border-radius: 50px 50px 50px 50px ;
  border: none;
  
}
.App .roundButton{
  margin-left: 7.5%;
  text-align: left;
  margin-top: 3%;

}

.App .roundButton button{
  margin-left: 1.5%;
  font-size: 60%;
  color: white;
  border-radius: 40px 40px 40px 40px ;
  border: none;
  background-color: white;
  height: 3.5em;
  width: 3.5em;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
